import { Component, ElementRef, HostListener, OnInit, Renderer } from "@angular/core";

import { Http, Response } from "@angular/http";

const INTERVAL_MILLISECONDS = 160;

@Component({
  selector: "app-reader",
  template: `
    <app-controls-modal *ngIf="!showReader && !showFullText"></app-controls-modal>
    <app-reader-display
      *ngIf="showReader && text"
      [word]="currentWord"
      (mousedown)="play = !play"
      (touchstart)="play = !play">
    </app-reader-display>
    <app-full-text *ngIf="showFullText && !showReader && !play"></app-full-text>
    <app-reader-controls *ngIf="showReader || showFullText"></app-reader-controls>
  `,
})
export class ReaderComponent implements OnInit {
  get text(): string | undefined {
    return this._text;
  }
  set text(value: string | undefined) {
    if (value === this.text) {
      return;
    }

    this._text = value;
    if (this.text == null) {
      this.splitText = [];
    }
    this.splitText = this.getSplitText(this.text);
  }
  _text: string | undefined;



  showReader: boolean = false;


  showFullText = false;



  splitText: string[];



  currentWord: string | undefined;



  idx: number = 0;



  wpm: number = 350;



  playTimeout: NodeJS.Timer | undefined;



  get play(): boolean {
    return this._play;
  }
  set play(value: boolean) {
    value = (value != null && value !== false);
    if (value === this.play) {
      return;
    }
    this._play = value;

    this.togglePlaying();
  }
  private _play: boolean = false;



  get intervalMs(): number {
    return Math.floor(60 * 1000 / this.wpm);
  }


  @HostListener("keydown.space", ["$event"])
  onClick(event: Event): void {
    this.onInteraction(event);
  }



  constructor(
    private elementRef: ElementRef,
    private renderer: Renderer,
    private http: Http) {
    this.initHostClass();
  }

  ngOnInit(): void {
    this.http.get("text.txt")
    .subscribe((res) => this.extractText(res));
  }

  extractText(res: Response): void {
    this.text = res.text();
  }

  initHostClass(): void {
    this.renderer.setElementClass(this.elementRef.nativeElement, "reader", true);
  }

  onInteraction(event: Event): void {
    event.preventDefault();

    if (this.text) {
      this.play = !this.play;
    }
  }

  resetReader(): void {
    this.idx = 0;
    this.play = false;
  }

  togglePlaying(): void {
    if (this.play) {
      this.startPlaying();
    } else {
      this.stopPlaying();
    }
  }

  setPlayTimeout(): void {
      this.playTimeout = setTimeout(() => {
      this.idx = this.idx + 1;
      this.currentWord = this.splitText[this.idx];

      if (this.currentWord == null) {
        this.resetReader();
      }
      this.setPlayTimeout();
    }, this.getIntervalFromWord(this.currentWord));
  }

  startPlaying(): void {
    const stepBackward = this.showFullText ? -10 : -1;
    this.incrementIdx(stepBackward);
    this.showFullText = false;
    this.currentWord = this.text[this.idx];
    this.setPlayTimeout();
  }

  stopPlaying(): void {
    if (this.playTimeout == null) {
      return;
    }

    clearTimeout(this.playTimeout);
   }

   incrementIdx(numWords: number) {
      this.idx = this.idx + numWords;
      if (this.idx < 0) {
        this.idx = 0;
      }
   }

  getSplitText(text: string): string[] {
    return text.split(/[\s-—–]+/);
  }

  getIntervalFromWord(word: string): number {
    const interval = this.intervalMs;
    if (!!~word.indexOf(".") || !!~word.indexOf("?") || !!~word.indexOf("!")) {
      return interval * 2;
    }
    if (!!~word.indexOf(";")) {
      return interval * 2;
    }
    if (!!~word.indexOf(",")) {
      return interval * 1.6;
    }
    if (word.length >= 8) {
      return interval * 1.5;
    }
    return interval;
  }
}



