import { AfterViewInit, AfterViewChecked, Component, ElementRef, Renderer, ViewChild } from "@angular/core";

import { ReaderComponent } from "./reader.component";

@Component({
  selector: "app-full-text",
  template: `
    <div *ngIf="splitText">
      <span>{{ splitText[0] }}</span>
      <span #focusEl class="reader__full-text__focus">{{ splitText[1] }}</span>
      <span>{{ splitText[2] }}</span>
    </div>
  `
})
export class FullTextComponent implements AfterViewInit, AfterViewChecked {
  @ViewChild("focusEl") focusEl: ElementRef;



  get splitText(): string[] | undefined {
    if (this.parent.text == null) {
      return;
    }

    if (this.prevSplitText != null && this.prevSplitIdx === this.parent.idx) {
      return this.prevSplitText;
    }

    this.updateSplitText();
    return this.prevSplitText;
  }


  prevSplitText: string[] | undefined;
  prevSplitIdx: number | undefined;



  constructor(
    private elementRef: ElementRef,
    private parent: ReaderComponent,
    private renderer: Renderer) {
    this.renderer.setElementClass(this.elementRef.nativeElement, "reader__full-text", true);
  }

  ngAfterViewChecked(): void {
    this.scrollToFocusEl();
  }

  ngAfterViewInit(): void {
    setTimeout(() => {
      this.scrollToFocusEl();
    });
  }

  updateSplitText(): void {
    const text: string[] = this.parent.getSplitText(this.parent.text);

    this.prevSplitIdx = this.parent.idx;
    if (this.parent.idx === 0) {
      this.prevSplitText = [
        "",
        text[this.parent.idx],
        text.slice(this.parent.idx + 1).join(" ")
      ];
      return;
    }

    this.prevSplitText = [
      text.slice(0, this.parent.idx).join(" "),
      text[this.parent.idx],
      text.slice(this.parent.idx + 1).join(" ")
    ];
  }

  scrollToFocusEl(): void {
    if (this.focusEl) {
      this.renderer.invokeElementMethod(this.focusEl.nativeElement, "scrollIntoView");
    }
  }
}



