import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

import { AppComponent } from './app.component';

import { ReaderComponent } from "./reader.component";
import { ControlsModalComponent } from "./controls-modal.component";
import { FullTextComponent } from "./full-text.component";
import { ReaderControlsComponent } from "./reader-controls.component";
import { ReaderDisplayComponent } from "./reader-display.component";

@NgModule({
  declarations: [
    AppComponent,
    ControlsModalComponent,
    FullTextComponent,
    ReaderControlsComponent,
    ReaderComponent,
    ReaderDisplayComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



