import { AfterViewInit, Component, ElementRef, Input, Renderer, ViewChild } from "@angular/core";

@Component({
  selector: "app-reader-display",
  template: `
    <span class="reader__display__word-focus" #focusEl>
    </span>
  `
})
export class ReaderDisplayComponent implements AfterViewInit {
  @ViewChild("focusEl") focusEl: ElementRef;



  @Input()
  get word(): string | undefined {
    return this._word;
  }
  set word(value: string | undefined) {
    this._word = value;

    this.showWord();
  }
  private _word: string | undefined;



  constructor(
    private renderer: Renderer,
    private elementRef: ElementRef) {

  }

  ngAfterViewInit(): void {
    this.initHostClass();
  }

  initHostClass(): void {
    this.renderer.setElementClass(this.elementRef.nativeElement, "reader__display", true);
  }

  showWord(): void {
    const splitWord = this.splitWord();

    this.renderer.setElementAttribute(this.focusEl.nativeElement, "data-chars-before-focus", splitWord[0]);
    this.renderer.setElementProperty(this.focusEl.nativeElement, "innerText", splitWord[1]);
    this.renderer.setElementAttribute(this.focusEl.nativeElement, "data-chars-after-focus", splitWord[2]);
  }

  splitWord(): string[] {
    if (this.word == null) {
      return [];
    }

    if (this.word.length === 1) {
      return ["", this.word, ""];
    }

    if (this.word.length === 2) {
      return ["", this.word[0], this.word[1]];
    }

    const focusIdx: number = Math.floor(this.word.length * 0.34);
    return [
      this.word.slice(0, focusIdx),
      this.word[focusIdx],
      this.word.slice(focusIdx + 1)
    ];
  }
}



