import { Component, ElementRef, Renderer } from "@angular/core";

import { ReaderComponent } from "./reader.component";

@Component({
  selector: "app-controls-modal",
  template: `
    <input class="input text-center" type="number" [(ngModel)]="parent.idx">
    <input class="input text-center" type="number" [(ngModel)]="parent.wpm">
    <button class="button" (click)="parent.showFullText = true">Go Go Go</button>
  `
})
export class ControlsModalComponent {
  constructor(
    private parent: ReaderComponent,
    private elementRef: ElementRef,
    private renderer: Renderer) {
      this.initHostClass();
  }

  initHostClass(): void {
    this.renderer.setElementClass(this.elementRef.nativeElement, "reader__controls-modal", true);
  }
}



