import { Component, ElementRef, HostListener, Renderer } from "@angular/core";

import { ReaderComponent } from "./reader.component";

@Component({
  selector: "app-reader-controls",
  template: `
    <span *ngIf="parent.text != null" class="reader__reader-controls__stats">
      {{ percentComplete }}% /
      {{ textLength }} words /
      {{ parent.wpm }} wpm /
      <span
        (touchstart)="parent.play = false; parent.showFullText = false; parent.showReader = false;"
        (mousedown)="parent.play = false; parent.showFullText = false; parent.showReader = false;">
        show options
      </span>
    </span>
    <span *ngIf="parent.text != null && showInput">
      <input type="number" [(ngModel)]="parent.idx">
    </span>
    <span *ngIf="parent.text != null" class="reader__reader-controls__buttons">
      <svg
        id="ff-button"
        class="reversed"
        (click)="parent.incrementIdx(-50)"
        xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" version="1.1" x="0px" y="0px" viewBox="0 0 100 100" enable-background="new 0 0 100 100" xml:space="preserve"><path d="M50,0C22.4,0,0,22.4,0,50c0,27.6,22.4,50,50,50s50-22.4,50-50C100,22.4,77.6,0,50,0z M78.5,54.1l-22,18.3  c-2.7,2.3-5,1.2-5-2.3V54.8L30.4,72.5c-2.7,2.3-5,1.2-5-2.3V29.9c0-3.5,2.2-4.6,5-2.3l21.2,17.7V29.9c0-3.5,2.2-4.6,5-2.3l22,18.3  C81.2,48.1,81.2,51.9,78.5,54.1z"/></svg>
      <svg
        id="ff-button"
        (click)="parent.incrementIdx(50)"
        xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" version="1.1" x="0px" y="0px" viewBox="0 0 100 100" enable-background="new 0 0 100 100" xml:space="preserve"><path d="M50,0C22.4,0,0,22.4,0,50c0,27.6,22.4,50,50,50s50-22.4,50-50C100,22.4,77.6,0,50,0z M78.5,54.1l-22,18.3  c-2.7,2.3-5,1.2-5-2.3V54.8L30.4,72.5c-2.7,2.3-5,1.2-5-2.3V29.9c0-3.5,2.2-4.6,5-2.3l21.2,17.7V29.9c0-3.5,2.2-4.6,5-2.3l22,18.3  C81.2,48.1,81.2,51.9,78.5,54.1z"/></svg>
      <svg
        id="play-button"
        *ngIf="!parent.play"
        (click)="parent.play = true; parent.showReader = true; parent.showFullText = false;"
        xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" version="1.1" x="0px" y="0px" viewBox="0 0 48 48" style="enable-background:new 0 0 48 48;" xml:space="preserve"><g><path d="M24,2C11.8,2,2,11.8,2,24s9.8,22,22,22s22-9.8,22-22S36.2,2,24,2z M33.1,25.7l-12,8c-0.9,0.6-2.2,0.4-2.8-0.6   C18.1,32.8,18,32.4,18,32V16c0-1.1,0.9-2,2-2c0.4,0,0.8,0.1,1.1,0.3l12,8c0.9,0.6,1.2,1.9,0.5,2.8C33.5,25.3,33.3,25.5,33.1,25.7z"/></g></svg>
        <svg
          id="pause-button"
          *ngIf="parent.play"
          (click)="parent.play = false; parent.showReader = false; parent.showFullText = true;"
          xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" version="1.1" x="0px" y="0px" viewBox="0 0 48 48" style="enable-background:new 0 0 48 48;" xml:space="preserve"><g><path d="M24,1C11.3,1,1,11.3,1,24s10.3,23,23,23s23-10.3,23-23S36.7,1,24,1z M22,30.5c0,1.1-0.9,2-2,2s-2-0.9-2-2v-13   c0-1.1,0.9-2,2-2s2,0.9,2,2V30.5z M30,30.5c0,1.1-0.9,2-2,2s-2-0.9-2-2v-13c0-1.1,0.9-2,2-2s2,0.9,2,2V30.5z"/></g></svg>
    </span>
  `
})
export class ReaderControlsComponent {
  get percentComplete(): number {
    return Math.floor(this.parent.idx / this.parent.splitText.length * 1000) / 10;
  }



  get textLength(): string {
    if (this.parent.text == null) {
      return "0";
    }
    return this.parent.splitText.length.toLocaleString();
  }



  @HostListener("mouseenter")
  onMouseEnter(): void {
    this.showInput = true;
  }

  @HostListener("mouseleave")
  onMouseLeave(): void {
    this.showInput = false;
  }



  showInput: boolean = false;



  constructor(
    private elementRef: ElementRef,
    private renderer: Renderer,
    private parent: ReaderComponent) {
      this.renderer.setElementClass(this.elementRef.nativeElement, "reader__reader-controls", true);
    }
}



